.nav {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 0 20px;
    height: 60px;
    z-index: 100;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav_black {
    background-color: #111;
}

.nav_logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.icon_wrap_nav {
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}

.nav_title {
    font-size: 25px;
    color: red;
    font-weight: bold;
    text-decoration: none;
}